export default {
  data(){
    return{
      isNavOnTop: true,
      isMenuOpen: false,
      colorButton : "#fff"
    }
  },

  mounted(){
     if (process.browser) {
      window.onscroll = this.onScrollNavbarColor
    }

  },

  methods: {

    onScrollNavbarColor(){
      if(window.scrollY > 95){
        this.isNavOnTop = false
        this.colorButton = "#222222"
      }else{
        this.isNavOnTop = true
        this.colorButton = "#fff"
      }
    },

    show(){
      this.isMenuOpen = !this.isMenuOpen
    }

  }

}
